import React from "react";
import PropTypes from "prop-types";
import logo from "../../assets/logo.png";
import "./header.scss";
import { Input, Button, Badge } from "antd";
import { SearchOutlined, ShoppingOutlined } from "@ant-design/icons";
import { NavLink } from "react-router-dom";

const Header = (props) => {
  return (
    <div className="shoppeHeader ">
      <div className="navBarWrap container">
        <div>
          <NavLink to="#" className="navLink">
            Kênh người bán
          </NavLink>
          <NavLink to="#" className="navLink">
            Tải ứng dụng
          </NavLink>
          <span className="navLink">
            Kết nối
            <NavLink to="#">
              <ion-icon name="logo-facebook"></ion-icon>
              <ion-icon name="logo-instagram"></ion-icon>
            </NavLink>
          </span>
        </div>
        <React.Fragment>
          <NavLink to="#">
            <ion-icon name="logo-facebook"></ion-icon>
            <ion-icon name="logo-instagram"></ion-icon>
          </NavLink>
        </React.Fragment>
      </div>
      <div className="wrapLogoSearch container">
        <div
          style={{
            display: "flex",
            height: "100%",
            alignItems: "center",
            width: "350px",
          }}
        >
          <ShoppingOutlined className="logo" />
          <h2 className="textLogo">My Shop </h2>
        </div>

        <div className="searchAndKey">
          <Input.Group style={inputGroupStyle}>
            <Input style={inputStyle} />
            <Button
              type="primary"
              icon={
                <SearchOutlined
                  style={{
                    fontSize: 25,
                    color: "rgb(246, 63, 46)",
                  }}
                />
              }
              onClick={() => alert("button")}
            />
          </Input.Group>

          <div className="keySearch">
            <NavLink to="#" className="navLink">
              áo khoác nam
            </NavLink>
            <NavLink to="#" className="navLink">
              Sandal Nữ
            </NavLink>
            <NavLink to="#" className="navLink">
              Giầy nữ
            </NavLink>
            <NavLink to="#" className="navLink">
              sách kỹ năng
            </NavLink>
            <NavLink to="#" className="navLink">
              Balo nữ
            </NavLink>
            <NavLink to="#" className="navLink">
              áo thun tay ngắn
            </NavLink>
          </div>
        </div>

        {/* <Badge count={2}> */}
        <Button
          type="primary"
          shape="circle"
          icon={<ion-icon name="cart-outline"></ion-icon>}
        />
        {/* </Badge> */}
      </div>
    </div>
  );
};

const inputGroupStyle = {
  backgroundColor: "#fff",
  display: "flex",
  alignItems: "center",
  width: "100%",

  boxSizing: "border-box",
  borderRadius: 5,
};

const inputStyle = {
  border: "none",
  outline: "none",
  margin: 0,
  height: 45,
  width: "100%",
  fontSize: 20,
  padding: "0 20px",
};

Header.propTypes = {};

export default Header;
