import React from "react";

import "./App.css";
import {
  HashRouter,
  Router,
  BrowserRouter,
  Switch,
  Route,
} from "react-router-dom";

import Header from "./components/Header";
import Home from "./pages/Home/index";
import "./App.css";

function App() {
  return (
    <BrowserRouter>
      <div className="App">
        <Header />
        <div className="main">
          <Home />
        </div>
      </div>
    </BrowserRouter>
  );
}

export default App;
